body {
  margin: 0;
  padding: 0;
  font-family: "Roboto Mono", monospace;
  text-align: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
  font-family: 'Montserrat', sans-serif;
  color: white;
}

h2 {
  font-size: 40px;
  font-family: 'Montserrat', sans-serif;
}

h3 {
  font-size: 30px;
  font-family: 'Montserrat', sans-serif;
}

@media screen and (max-width: 800px)
{
  p {
    font-size: 15px;
  }
}

@media screen and (max-width: 700px)
{
  p {
    font-size: 13px;
  }
}

@media screen and (max-width: 600px)
{
  p {
    font-size: 10px;
  }
}

.App footer {
  background-color: black;
  margin: 0;
}

.App footer p {
  margin: 0;
  padding: 1rem;
  color: white;
}

.App footer a {
  color: white;
  font-weight: bold;
  text-decoration: none;
}
input {
  width: 25rem;
  height: 1.5rem;
  border-radius: 10px;
  border: none;
  padding: 0 20px;
  font-family: "Roboto Mono", monospace;
  background-color: rgba(255,255,255, 0.8);
  outline: none;
}

.HomePageSearch > form > label > button {
  border-radius: 10px;
  background-color: grey;
  border: none;
  padding: 3px 5px;
  cursor: pointer;
  margin-left: 15px;
}

.HomePageSearch > form > label {
  display: flex;
  flex-direction: row;
}
.OneReviewPreview {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
  align-items: center;
}

.ReviewPreview {
  border-radius: 10px;
  background-color: rgb(228, 227, 227);
  width: 32%;
  height: 15rem;
  border-radius: 8px;
  display: flex;
  flex-direction: row;
}

.ReviewerPicture {
  padding-top: 1rem;
  width: 35%;
  height: 100%;
}

.userPicture {
  width: 60%;
  border-radius: 50%;
}

.ReviewerPicture > p{
  font-size: 1rem;
}

.ReviewInfo {
  width: 65%;
  height: 100%;
}

.ReviewInfo > p {
  word-break: break-all;
  font-size: 1rem;
}

.ReviewInfo > h2 {
  margin-top: 20px;
}

.RestaurantPicturePreview {
  width: 100%;
}

.RestaurantInfoOverlay {
  position: absolute;
  display: flex;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  max-width: 100%;
  max-height: 100%;
  transition: 0.5s ease;
  background-color: rgba(68, 69, 71, 0.7);
  opacity: 0;
  display: block;
}

.RestaurantInfoOverlay > p {
  margin: 0;
  color: white;
  padding-top: 2rem;
}
.OneRestaurantPicturePreview:hover .RestaurantInfoOverlay {
  opacity: 1;
}

.OneRestaurantPicturePreview {
  position: relative;
  width: 18rem;
  margin: 10px;
  height: 15rem;
  overflow: hidden;

}

.OneRestaurantPicturePreview img {
  display: flex;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  /*bottom: 0;
  display: block;
  object-fit: contain; */
}

.ListofRestaurantPicturePreview {
  list-style: none;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 0;
  align-items: center;
  justify-content: center;
}


